
/* Stili generali */
.home-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  text-align: center;
  background: linear-gradient(135deg, #ff9a9e, #fad0c4); /* Gradiente rosa */
  overflow: hidden; /* Impedisce lo scrolling */
  position: fixed; /* Mantiene il contenitore fisso */
  top: 50%; /* Posiziona al centro verticalmente */
  left: 50%; /* Posiziona al centro orizzontalmente */
  transform: translate(-50%, -50%); /* Centra perfettamente */
  width: 100%; /* Occupa tutta la larghezza */
  max-width: 100%; /* Massima larghezza */
  user-select: none; /* Impedisce la selezione del testo */
  -webkit-user-drag: none; /* Impedisce il trascinamento su Safari */
}

/* Contenitore principale */
.home-content {
  padding: 20px;
  background: rgba(255, 255, 255, 0.9); /* Sfondo bianco con trasparenza */
  border-radius: 20px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.15); /* Ombra pi첫 pronunciata */
  max-width: 90%;
  width: 400px;
}

/* Immagine GIF */
.funny-gif {
  width: 250px; /* Riduci leggermente le dimensioni */
  height: 250px; /* Riduci leggermente le dimensioni */
  object-fit: cover;
  margin-bottom: 15px; /* Riduci il margine inferiore */
  border-radius: 15px; /* Bordi arrotondati */
}

/* Titolo */
h2 {
  font-size: 22px; /* Riduci leggermente la dimensione del font */
  color: #ff4d6d; /* Colore rosa acceso */
  margin-bottom: 15px; /* Aggiungi margine inferiore */
}

/* Contenitore dei pulsanti */
.buttons {
  display: flex;
  gap: 20px; /* Spazio tra i pulsanti */
  margin-top: 10px;
}

/* Pulsanti */
.yes-button,
.no-button {
  padding: 10px 20px;
  border-radius: 25px; /* Bordi pi첫 arrotondati */
  border: none;
  cursor: pointer;
  font-size: 16px; /* Riduci leggermente la dimensione del font */
  transition: all 0.3s ease-in-out; /* Transizione fluida */
}

.yes-button {
  background-color: #ff4d6d; /* Colore rosa acceso */
  color: white;
}

.yes-button:hover {
  background-color: #ff3355; /* Colore pi첫 scuro al passaggio del mouse */
  transform: scale(1.05); /* Effetto hover */
}

.no-button {
  background-color: #888; /* Colore grigio */
  color: white;
}

.no-button:hover {
  background-color: #666; /* Colore pi첫 scuro al passaggio del mouse */
  transform: scale(1.05); /* Effetto hover */
}

/* Media Query per dispositivi mobili */
@media (max-width: 480px) {
  .home-content {
    padding: 15px; /* Riduci ulteriormente il padding */
    max-width: 90%; /* Larghezza massima ridotta */
  }

  .funny-gif {
    width: 200px; /* Riduci ulteriormente le dimensioni */
    height: 200px; /* Riduci ulteriormente le dimensioni */
  }

  h2 {
    font-size: 20px; /* Riduci ulteriormente la dimensione del font */
  }

  .buttons {
    gap: 15px; /* Riduci lo spazio tra i pulsanti */
  }

  .yes-button,
  .no-button {
    padding: 8px 16px; /* Riduci il padding */
    font-size: 14px; /* Riduci ulteriormente la dimensione del font */
  }
}

.yes-button {
  transition: transform 0.3s ease; /* Aggiungi una transizione fluida */
}
