.App {
    text-align: center;
    background: linear-gradient(135deg, #ff9a9e, #fad0c4);
    min-height: calc(100vh - env(safe-area-inset-bottom));
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: 'Poppins', sans-serif;
    color: #4a4a4a;
    padding: 10px;
    padding-bottom: env(safe-area-inset-bottom); /* Aggiunge spazio per la barra */
    box-sizing: border-box;
  }
  
  .App-header {
    padding: 15px;
    background: rgba(255, 255, 255, 0.9);
    border-radius: 20px;
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.15);
    width: 85%;
    max-width: 80px;
    animation: fadeIn 1s ease-in-out;
    position: relative;
    transform: none;
    box-sizing: border-box;
  }
  
  h1 {
    font-size: 1.5rem;
    color: #ff4d6d;
    margin-bottom: 10px;
    font-weight: 600;
  }
  
  .video {
    width: 100%;
    max-height: 80vh; /* Riduci l'altezza massima per evitare il taglio */
    border-radius: 15px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
    margin-bottom: 10px;
  }
  
  .timer {
    margin: 10px 0;
    font-size: 1rem;
    color: #333;
    font-weight: 500;
  }
  
  p {
    font-size: 0.9rem;
    color: #ff4d6d;
    margin: 10px 0;
    font-weight: 500;
  }
  
  .back-button {
    background-color: #ff4d6d;
    color: white;
    border: none;
    padding: 8px 16px;
    font-size: 0.8rem;
    border-radius: 25px;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    margin-bottom: env(safe-area-inset-bottom); /* Spazio per la barra di Safari */
  }
  
  .back-button:hover {
    background-color: #ff3355;
    transform: scale(1.1);
    box-shadow: 0 6px 10px rgba(0, 0, 0, 0.2);
  }
  
  /* Media Query per dispositivi mobili */
  @media (max-width: 480px) {
    .App-header {
      padding: 10px;
      max-width: 70%;
    }
  
    h1 {
      font-size: 1.3rem;
    }
  
    .timer {
      font-size: 0.9rem;
    }
  
    p {
      font-size: 0.8rem;
    }
  
    .back-button {
      padding: 6px 12px;
      font-size: 0.7rem;
    }
  }  